          <div class="market-options">
            <label class="market-option">
              <input 
                type="radio" 
                v-model="strategy.marketType" 
                value="bull"
                class="market-radio"
              >
              <div class="market-card bull">
                <div class="market-icon">üìà</div>
                <div class="market-label">ÏÉÅÏäπÏû• Ï†ÑÎûµ</div>
                <div class="market-desc">Î™®Î©òÌÖÄ + ÏÑ±Ïû•Ï£º Ï§ëÏã¨</div>
              </div>
            </label>
            
            <label class="market-option">
              <input 
                type="radio" 
                v-model="strategy.marketType" 
                value="bear"
                class="market-radio"
              >
              <div class="market-card bear">
                <div class="market-icon">üìâ</div>
                <div class="market-label">ÌïòÎùΩÏû• Ï†ÑÎûµ</div>
                <div class="market-desc">Í∞ÄÏπòÏ£º + Î∞∞ÎãπÏ£º Ï§ëÏã¨</div>
              </div>
            </label>
          </div>
        </div>

        <!-- Ìà¨Ïûê ÏßÄÏó≠ ÏÑ†ÌÉù -->
        <div class="form-group">
          <label class="form-label">Ìà¨Ïûê ÏßÄÏó≠</label>
          <div class="region-options">
            <label class="region-option">
              <input 
                type="radio" 
                v-model="strategy.region" 
                value="domestic"
                class="region-radio"
              >
              <div class="region-card">
                <span class="region-flag">üá∞üá∑</span>
                <span class="region-text">Íµ≠ÎÇ¥ Ìà¨Ïûê</span>
              </div>
            </label>
            
            <label class="region-option">
              <input 
                type="radio" 
                v-model="strategy.region" 
                value="global"
                class="region-radio"
              >
              <div class="region-card">
                <span class="region-flag">üåç</span>
                <span class="region-text">Ìï¥Ïô∏ Ìà¨Ïûê</span>
              </div>
            </label>
          </div>
        </div>

        <!-- Ï∂îÏ≤ú Ï†ÑÎûµ ÌëúÏãú -->
        <div v-if="bestStrategy" class="best-strategy">
          <div class="strategy-header">
            <span class="strategy-badge">AI Ï∂îÏ≤ú Ï†ÑÎûµ</span>
            <span class="strategy-performance">
              ÏòàÏÉÅ ÏàòÏùµÎ•†: {{ bestStrategy.expectedReturn }}%
            </span>
          </div>
          <div class="strategy-info">
            <p><strong>{{ bestStrategy.name }}</strong></p>
            <p>{{ bestStrategy.description }}</p>
            <div class="recommended-stocks">
              <h4>Ï∂îÏ≤ú Ï¢ÖÎ™© Íµ¨ÏÑ±</h4>
              <div class="stock-chips">
                <span 
                  v-for="stock in bestStrategy.stocks" 
                  :key="stock.code"
                  class="stock-chip"
                >
                  {{ stock.name }} ({{ stock.allocation }}%)
                </span>
              </div>
            </div>
          </div>
          <button 
            @click="applyBestStrategy"
            class="btn btn-sm btn-outline"
          >
            Ïù¥ Ï†ÑÎûµ Ï†ÅÏö©
          </button>
        </div>

        <!-- Ï¢ÖÎ™© Íµ¨ÏÑ± ÏÑ§Ï†ï -->
        <div class="form-group">
          <label class="form-label">Ï¢ÖÎ™© Íµ¨ÏÑ±</label>
          <div class="stocks-container">
            <div 
              v-for="(stock, index) in strategy.stocks" 
              :key="index"
              class="stock-item"
            >
              <div class="stock-inputs">
                <input 
                  type="text" 
                  v-model="stock.code"
                  :placeholder="strategy.region === 'domestic' ? 'Ï¢ÖÎ™©ÏΩîÎìú (Ïòà: 005930)' : 'Ìã∞Ïª§ (Ïòà: AAPL)'"
                  class="form-input stock-code"
                  @blur="validateStockCode(index)"
                >
                <input 
                  type="text" 
                  v-model="stock.name"
                  placeholder="Ï¢ÖÎ™©Î™Ö"
                  class="form-input stock-name"
                  readonly
                >
                <div class="allocation-input-group">
                  <input 
                    type="number" 
                    v-model="stock.allocation"
                    min="1"
                    max="100"
                    placeholder="ÎπÑÏú®"
                    class="form-input allocation-input"
                    @input="updateTotalAllocation"
                  >
                  <span class="allocation-unit">%</span>
                </div>
                <button 
                  v-if="strategy.stocks.length > 1"
                  @click="removeStock(index)"
                  class="btn-remove"
                  type="button"
                >
                  √ó
                </button>
              </div>
              <div v-if="stock.error" class="form-error">
                {{ stock.error }}
              </div>
            </div>
            
            <button 
              @click="addStock"
              class="btn btn-outline btn-sm add-stock-btn"
              type="button"
            >
              + Ï¢ÖÎ™© Ï∂îÍ∞Ä
            </button>
            
            <div class="allocation-summary">
              <span class="total-allocation" :class="{ 'invalid': totalAllocation !== 100 }">
                Ï¥ù Ìà¨Ïûê ÎπÑÏú®: {{ totalAllocation }}%
              </span>
              <span v-if="totalAllocation !== 100" class="allocation-warning">
                (Ï¥ù 100%Í∞Ä ÎêòÏñ¥Ïïº Ìï©ÎãàÎã§)
              </span>
            </div>
          </div>
        </div>
      </div>
      
      <div class="card-footer">
        <div class="strategy-actions">
          <div class="trading-status">
            <span v-if="isTrading" class="status-indicator active">
              <span class="status-dot"></span>
              ÏûêÎèôÎß§Îß§ Ïã§Ìñâ Ï§ë
            </span>
            <span v-else class="status-indicator inactive">
              <span class="status-dot"></span>
              ÏûêÎèôÎß§Îß§ ÎåÄÍ∏∞ Ï§ë
            </span>
          </div>
          
          <div class="action-buttons">
            <button 
              v-if="!isTrading"
              @click="startTrading"
              :disabled="!isValidStrategy || loading"
              class="btn btn-success"
            >
              <span v-if="loading" class="loading-spinner"></span>
              ÏûêÎèôÎß§Îß§ ÏãúÏûë
            </button>
            
            <button 
              v-if="isTrading"
              @click="stopTrading"
              :disabled="loading"
              class="btn btn-danger"
            >
              <span v-if="loading" class="loading-spinner"></span>
              ÏûêÎèôÎß§Îß§ Ï¢ÖÎ£å
            </button>
          </div>
        </div>
