  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'TradingStrategy',
  data() {
    return {
      strategy: {
        marketType: 'bull',
        region: 'domestic',
        stocks: [
          { code: '', name: '', allocation: 100, error: null }
        ]
      },
      totalAllocation: 100
    }
  },
  computed: {
    ...mapGetters('trading', ['bestStrategy', 'isTrading', 'isLoading', 'currentStrategy']),
    
    loading() {
      return this.isLoading
    },
    
    isValidStrategy() {
      return this.strategy.marketType && 
             this.strategy.region &&
             this.strategy.stocks.every(stock => stock.code && stock.allocation > 0) &&
             this.totalAllocation === 100 &&
             !this.strategy.stocks.some(stock => stock.error)
    }
  },
  async created() {
    await this.loadBestStrategy()
    await this.loadTradingStatus()
    
    // ÌòÑÏû¨ ÌôúÏÑ± Ï†ÑÎûµÏù¥ ÏûàÏúºÎ©¥ ÌèºÏóê Î°úÎìú
    if (this.currentStrategy) {
      this.loadCurrentStrategy()
    }
  },
  methods: {
    ...mapActions('trading', [
      'loadBestStrategy', 
      'loadTradingStatus', 
      'createStrategy',
      'startTrading as startTradingAction',
      'stopTrading as stopTradingAction'
    ]),
    
    loadCurrentStrategy() {
      if (this.currentStrategy) {
        this.strategy = {
          marketType: this.currentStrategy.marketType,
          region: this.currentStrategy.region,
          stocks: this.currentStrategy.stocks || [{ code: '', name: '', allocation: 100, error: null }]
        }
        this.updateTotalAllocation()
      }
    },
    
    applyBestStrategy() {
      if (this.bestStrategy) {
        this.strategy = {
          marketType: this.bestStrategy.marketType,
          region: this.bestStrategy.region,
          stocks: this.bestStrategy.stocks.map(stock => ({
            code: stock.code,
            name: stock.name,
            allocation: stock.allocation,
            error: null
          }))
        }
        this.updateTotalAllocation()
      }
    },
    
    addStock() {
      if (this.strategy.stocks.length < 10) { // ÏµúÎåÄ 10Í∞ú Ï¢ÖÎ™©
        this.strategy.stocks.push({ 
          code: '', 
          name: '', 
          allocation: 0, 
          error: null 
        })
      }
    },
    
    removeStock(index) {
      this.strategy.stocks.splice(index, 1)
      this.updateTotalAllocation()
    },
    
    updateTotalAllocation() {
      this.totalAllocation = this.strategy.stocks.reduce((sum, stock) => {
        return sum + (parseInt(stock.allocation) || 0)
      }, 0)
    },
    
    async validateStockCode(index) {
      const stock = this.strategy.stocks[index]
      if (!stock.code) {
        stock.error = null
        stock.name = ''
        return
      }
      
      try {
        // Ï¢ÖÎ™© ÏΩîÎìú Ïú†Ìö®ÏÑ± Í≤ÄÏÇ¨ Î∞è Ï¢ÖÎ™©Î™Ö Ï°∞Ìöå
        if (this.strategy.region === 'domestic') {
          // Íµ≠ÎÇ¥ Ï£ºÏãù ÏΩîÎìú Í≤ÄÏ¶ù (6ÏûêÎ¶¨ Ïà´Ïûê)
          if (!/^\d{6}$/.test(stock.code)) {
            stock.error = 'Ïò¨Î∞îÎ•∏ Ï¢ÖÎ™© ÏΩîÎìúÎ•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî (6ÏûêÎ¶¨ Ïà´Ïûê)'
            stock.name = ''
            return
          }
          
          // Ïã§Ï†úÎ°úÎäî APIÎ•º Ìò∏Ï∂úÌïòÏó¨ Ï¢ÖÎ™©Î™ÖÏùÑ Ï°∞ÌöåÌï¥Ïïº Ìï®
          const stockNames = {
            '005930': 'ÏÇºÏÑ±Ï†ÑÏûê',
            '000660': 'SKÌïòÏù¥ÎãâÏä§',  
            '035420': 'NAVER',
            '051910': 'LGÌôîÌïô',
            '006400': 'ÏÇºÏÑ±SDI',
            '035720': 'Ïπ¥Ïπ¥Ïò§',
            '207940': 'ÏÇºÏÑ±Î∞îÏù¥Ïò§Î°úÏßÅÏä§',
            '373220': 'LGÏóêÎÑàÏßÄÏÜîÎ£®ÏÖò',
            '000270': 'Í∏∞ÏïÑ',
            '068270': 'ÏÖÄÌä∏Î¶¨Ïò®'
          }
          
          stock.name = stockNames[stock.code] || 'Ïïå Ïàò ÏóÜÎäî Ï¢ÖÎ™©'
          stock.error = null
        } else {
          // Ìï¥Ïô∏ Ï£ºÏãù Ìã∞Ïª§ Í≤ÄÏ¶ù
          if (!/^[A-Z]{1,5}$/.test(stock.code.toUpperCase())) {
            stock.error = 'Ïò¨Î∞îÎ•∏ Ìã∞Ïª§Î•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî'
            stock.name = ''
            return
          }
          
          // Ìï¥Ïô∏ Ï£ºÏãù ÏòàÏãú
          const globalStockNames = {
            'AAPL': 'Apple Inc.',
            'MSFT': 'Microsoft Corp.',
            'GOOGL': 'Alphabet Inc.',
            'AMZN': 'Amazon.com Inc.',
            'TSLA': 'Tesla Inc.',
            'META': 'Meta Platforms Inc.',
            'NVDA': 'NVIDIA Corp.',
            'NFLX': 'Netflix Inc.'
          }
          
          stock.code = stock.code.toUpperCase()
          stock.name = globalStockNames[stock.code] || 'Ïïå Ïàò ÏóÜÎäî Ï¢ÖÎ™©'
          stock.error = null
        }
      } catch (error) {
        stock.error = 'Ï¢ÖÎ™© Ï†ïÎ≥¥Î•º Í∞ÄÏ†∏Ïò¨ Ïàò ÏóÜÏäµÎãàÎã§'
        stock.name = ''
      }
    },
    
    async startTrading() {
      try {
        const success = await this.createStrategy(this.strategy)
        
        if (success) {
          const latestStrategy = this.currentStrategy
          if (latestStrategy) {
            await this.startTradingAction(latestStrategy.id)
          }
        }
      } catch (error) {
        console.error('ÏûêÎèôÎß§Îß§ ÏãúÏûë Ïò§Î•ò:', error)
      }
    },
    
    async stopTrading() {
      try {
        await this.stopTradingAction()
      } catch (error) {
        console.error('ÏûêÎèôÎß§Îß§ Ï§ëÎã® Ïò§Î•ò:', error)
      }
    }
  }
}
</script>

