<script>
import { mapGetters, mapActions } from 'vuex'
import TradingStrategy from '@/components/TradingStrategy.vue'
import apiClient from '@/utils/api'

export default {
  name: 'TradingPage',
  components: {
    TradingStrategy
  },
  data() {
    return {
      tradingHistory: [],
      historyLoading: false,
      displayLimit: 10
    }
  },
  computed: {
    ...mapGetters('trading', ['isTrading', 'currentStrategy', 'isLoading']),
    
    hasOrders() {
      return this.tradingHistory.length > 0
    },
    
    displayedOrders() {
      return this.tradingHistory.slice(0, this.displayLimit)
    }
  },
  async created() {
    try {
      await this.loadTradingStatus()
      await this.loadTradingHistory()
    } catch (error) {
      console.error('ÌéòÏù¥ÏßÄ Ï¥àÍ∏∞Ìôî Ïò§Î•ò:', error)
    }
  },
  methods: {
    ...mapActions('trading', ['loadTradingStatus']),
    
    async loadTradingHistory() {
      this.historyLoading = true
      try {
        const response = await apiClient.get('/trading/history')
        if (response.data.success) {
          this.tradingHistory = response.data.data || []
        }
      } catch (error) {
        console.error('Îß§Îß§ Ïù¥Î†• Î°úÎìú Ïã§Ìå®:', error)
        this.tradingHistory = []
        
        // ToastÍ∞Ä ÏóÜÏùÑ Í≤ΩÏö∞Î•º ÎåÄÎπÑÌïú Ï°∞Í±¥Î∂Ä Ïã§Ìñâ
        if (this.$toast) {
          this.$toast.error('Îß§Îß§ Ïù¥Î†•ÏùÑ Î∂àÎü¨Ïò§ÎäîÎç∞ Ïã§Ìå®ÌñàÏäµÎãàÎã§.')
        } else {
          console.warn('Îß§Îß§ Ïù¥Î†•ÏùÑ Î∂àÎü¨Ïò§ÎäîÎç∞ Ïã§Ìå®ÌñàÏäµÎãàÎã§.')
        }
      } finally {
        this.historyLoading = false
      }
    },
    
    async refreshHistory() {
      await this.loadTradingHistory()
      
      if (this.$toast) {
        this.$toast.success('Îß§Îß§ Ïù¥Î†•Ïù¥ ÏÉàÎ°úÍ≥†Ïπ®ÎêòÏóàÏäµÎãàÎã§.')
      }
    },
    
    showMore() {
      this.displayLimit += 10
    },
    
    formatDateTime(dateString) {
      if (!dateString) return '-'
      
      try {
        const date = new Date(dateString)
        if (isNaN(date.getTime())) return '-'
        
        const now = new Date()
        const diffMs = now - date
        const diffMins = Math.floor(diffMs / 60000)
        const diffHours = Math.floor(diffMins / 60)
        const diffDays = Math.floor(diffHours / 24)
        
        if (diffMins < 1) return 'Î∞©Í∏à Ï†Ñ'
        if (diffMins < 60) return `${diffMins}Î∂Ñ Ï†Ñ`
        if (diffHours < 24) return `${diffHours}ÏãúÍ∞Ñ Ï†Ñ`
        if (diffDays < 7) return `${diffDays}Ïùº Ï†Ñ`
        
        return date.toLocaleDateString('ko-KR', {
          month: 'short',
          day: 'numeric',
          hour: '2-digit',
          minute: '2-digit'
        })
      } catch (error) {
        console.error('ÎÇ†Ïßú Ìè¨Îß∑ Ïò§Î•ò:', error)
        return dateString
      }
    },
    
    formatPrice(price, region) {
      if (!price || price === null || price === undefined) return '-'
      
      try {
        const numPrice = typeof price === 'string' ? parseFloat(price) : price
        if (isNaN(numPrice)) return '-'
        
        if (region === 'domestic') {
          return numPrice.toLocaleString() + 'Ïõê'
        } else {
          return '$' + numPrice.toFixed(2)
        }
      } catch (error) {
        console.error('Í∞ÄÍ≤© Ìè¨Îß∑ Ïò§Î•ò:', error)
        return '-'
      }
    },
    
    formatNumber(number) {
      if (!number || number === null || number === undefined) return '0'
      
      try {
        const num = typeof number === 'string' ? parseInt(number) : number
        if (isNaN(num)) return '0'
        return num.toLocaleString()
      } catch (error) {
        console.error('Ïà´Ïûê Ìè¨Îß∑ Ïò§Î•ò:', error)
        return '0'
      }
    },
    
    getStatusText(status) {
      if (!status) return 'Ïïå Ïàò ÏóÜÏùå'
      
      const statusMap = {
        'PENDING': 'ÎåÄÍ∏∞',
        'FILLED': 'Ï≤¥Í≤∞',
        'PARTIALLY_FILLED': 'Î∂ÄÎ∂ÑÏ≤¥Í≤∞',
        'CANCELLED': 'Ï∑®ÏÜå',
        'REJECTED': 'Í±∞Î∂Ä'
      }
      return statusMap[status] || status
    }
  }
}
</script>

